          <p>Êü•ÁúãÁâ©‰∏öË¥π„ÄÅÊ∞¥ÁîµË¥πÁ≠âË¥¶Âçï‰ø°ÊÅØ</p>
          <button class="btn" @click="handlePropertyFee">Êü•ËØ¢Ë¥¶Âçï</button>
        </div>
