          <p>ÂàÜ‰∫´ÁîüÊ¥ªÂä®ÊÄÅÔºå‰∏éÈÇªÂ±Ö‰∫íÂä®‰∫§ÊµÅ</p>
          <button class="btn" @click="handleCommunity">ËøõÂÖ•ÈÇªÈáåÂúà</button>
        </div>
        <div class="feature-card">
