<script>
import websocketService from '../services/websocket';
import { weatherApi } from '../services/api';

export default {
  name: 'Resident',
  data() {
    return {
      username: '',
      weatherTips: '',
      notification: '',
      userId: ''
    }
  },
  mounted() {
    // ‰ªéÊú¨Âú∞Â≠òÂÇ®Ëé∑ÂèñÁî®Êà∑‰ø°ÊÅØ
    this.username = localStorage.getItem('username');
    this.userId = localStorage.getItem('username'); // ÂÅáËÆæÁî®Êà∑ÂêçÂ∞±ÊòØÁî®Êà∑ID
    
    // ÂàùÂßãÂåñWebSocketËøûÊé•
    this.initWebSocket();
    
    // Ëé∑ÂèñÂ§©Ê∞î‰ø°ÊÅØ
    this.getWeatherInfo();
  },
  beforeUnmount() {
    // Êñ≠ÂºÄWebSocketËøûÊé•
    websocketService.disconnect();
  },
  methods: {
    // ÂàùÂßãÂåñWebSocket
    initWebSocket() {
      if (this.userId) {
        websocketService.connect(this.userId);
        
        // ËÆ¢ÈòÖÂ§©Ê∞îÊèêÈÜíÊ∂àÊÅØ
        websocketService.subscribe('weather_tips', (message) => {
          this.weatherTips = message.content;
          this.notification = 'Êî∂Âà∞Êñ∞ÁöÑÂ§©Ê∞îÊ∏©È¶®ÊèêÁ§∫ÔºÅ';
        });
        
        // ËÆ¢ÈòÖÂÖ®Â±ÄÊ∂àÊÅØ
        websocketService.subscribe('*', (message) => {
          console.log('Êî∂Âà∞ÂÖ®Â±ÄÊ∂àÊÅØ:', message);
        });
      }
    },
    
    // Ëé∑ÂèñÂ§©Ê∞î‰ø°ÊÅØ
    async getWeatherInfo() {
      try {
        const response = await weatherApi.getWeatherTips();
        if (response.success) {
          this.weatherTips = response.data;
        }
      } catch (error) {
        console.error('Ëé∑ÂèñÂ§©Ê∞î‰ø°ÊÅØÂ§±Ë¥•:', error);
      }
    },
    
    handleProfile() {
      // Ë∑≥ËΩ¨Âà∞‰∏™‰∫∫‰∏≠ÂøÉÈ°µÈù¢
      this.$router.push('/resident/profile');
    },
    
    handleRepair() {
      // Ë∑≥ËΩ¨Âà∞Êä•‰øÆÈ°µÈù¢
      this.$router.push('/resident/repairs');
    },
    
    handleLogout() {
      // Êñ≠ÂºÄWebSocketËøûÊé•
      websocketService.disconnect();
      
      // Ê∏ÖÈô§Êú¨Âú∞Â≠òÂÇ®ÁöÑtokenÂíåÁî®Êà∑‰ø°ÊÅØ
      localStorage.removeItem('token');
      localStorage.removeItem('username');
      localStorage.removeItem('role');
      localStorage.removeItem('realName');
      localStorage.removeItem('phone');
      localStorage.removeItem('email');
      localStorage.removeItem('address');
      localStorage.removeItem('idCard');
      
      // Ë∑≥ËΩ¨Âà∞ÁôªÂΩïÈ°µ
      this.$router.push('/login');
    },
    
    handleCommunity() {
      // Ë∑≥ËΩ¨Âà∞ÈÇªÈáåÂúàÈ°µÈù¢
      this.$router.push('/resident/community');
    },
    
    handleActivities() {
      // Ë∑≥ËΩ¨Âà∞Ê¥ªÂä®ÂàóË°®È°µÈù¢
      this.$router.push('/resident/activities');
    },
    
    handleAnnouncements() {
      // Ë∑≥ËΩ¨Âà∞ÂÖ¨ÂëäÂàóË°®È°µÈù¢
      this.$router.push('/resident/announcements');
    },
    
    handlePropertyFee() {
