// WebSocketÊúçÂä°

class WebSocketService {
    constructor() {
        this.socket = null;
        this.isConnected = false;
        this.reconnectAttempts = 0;
        this.maxReconnectAttempts = 5;
        this.reconnectDelay = 3000;
        this.listeners = new Map();
        this.userId = null;
    }

    // ËøûÊé•WebSocketÊúçÂä°Âô®
    connect(userId) {
        if (this.socket && this.isConnected) {
            console.log('WebSocketÂ∑≤ÁªèËøûÊé•');
            return;
        }

        this.userId = userId;
        const wsUrl = `ws://localhost:8080/backend/websocket/${userId}`;
        
        try {
            this.socket = new WebSocket(wsUrl);
            
            this.socket.onopen = () => {
                console.log('WebSocketËøûÊé•ÊàêÂäü');
                this.isConnected = true;
                this.reconnectAttempts = 0;
            };
            
            this.socket.onmessage = (event) => {
                console.log('Êî∂Âà∞WebSocketÊ∂àÊÅØ:', event.data);
                this.handleMessage(event.data);
            };
            
            this.socket.onclose = () => {
                console.log('WebSocketËøûÊé•ÂÖ≥Èó≠');
                this.isConnected = false;
                this.attemptReconnect();
            };
            
            this.socket.onerror = (error) => {
                console.error('WebSocketÈîôËØØ:', error);
                this.isConnected = false;
            };
        } catch (error) {
            console.error('WebSocketËøûÊé•Â§±Ë¥•:', error);
            this.isConnected = false;
            this.attemptReconnect();
        }
    }

    // ÈáçÊñ∞ËøûÊé•
    attemptReconnect() {
        if (this.reconnectAttempts < this.maxReconnectAttempts) {
            this.reconnectAttempts++;
            console.log(`Â∞ùËØïÈáçÊñ∞ËøûÊé•... (${this.reconnectAttempts}/${this.maxReconnectAttempts})`);
            
            setTimeout(() => {
                this.connect(this.userId);
            }, this.reconnectDelay);
        } else {
            console.error('WebSocketÈáçËøûÂ§±Ë¥•ÔºåÂ∑≤ËææÂà∞ÊúÄÂ§ßÈáçËØïÊ¨°Êï∞');
        }
    }

    // ÂèëÈÄÅÊ∂àÊÅØ
    sendMessage(message) {
        if (this.socket && this.isConnected) {
            try {
                this.socket.send(JSON.stringify(message));
                console.log('ÂèëÈÄÅWebSocketÊ∂àÊÅØ:', message);
            } catch (error) {
                console.error('ÂèëÈÄÅWebSocketÊ∂àÊÅØÂ§±Ë¥•:', error);
            }
        } else {
            console.error('WebSocketÊú™ËøûÊé•ÔºåÊó†Ê≥ïÂèëÈÄÅÊ∂àÊÅØ');
        }
    }

    // Â§ÑÁêÜÊé•Êî∂Âà∞ÁöÑÊ∂àÊÅØ
    handleMessage(messageData) {
        try {
            const message = JSON.parse(messageData);
            const messageType = message.type;
            
            // Ë∞ÉÁî®ÂØπÂ∫îÁöÑÁõëÂê¨Âô®
            if (this.listeners.has(messageType)) {
                const typeListeners = this.listeners.get(messageType);
                typeListeners.forEach(listener => {
                    try {
                        listener(message);
                    } catch (error) {
                        console.error('Â§ÑÁêÜWebSocketÊ∂àÊÅØÁõëÂê¨Âô®Â§±Ë¥•:', error);
                    }
                });
            }
            
            // Ë∞ÉÁî®ÂÖ®Â±ÄÁõëÂê¨Âô®
            if (this.listeners.has('*')) {
                const globalListeners = this.listeners.get('*');
                globalListeners.forEach(listener => {
                    try {
                        listener(message);
                    } catch (error) {
                        console.error('Â§ÑÁêÜWebSocketÂÖ®Â±ÄÁõëÂê¨Âô®Â§±Ë¥•:', error);
                    }
                });
            }
        } catch (error) {
            console.error('Ëß£ÊûêWebSocketÊ∂àÊÅØÂ§±Ë¥•:', error);
        }
    }

    // ËÆ¢ÈòÖÊ∂àÊÅØÁ±ªÂûã
    subscribe(messageType, listener) {
        if (!this.listeners.has(messageType)) {
            this.listeners.set(messageType, []);
        }
        this.listeners.get(messageType).push(listener);
        
        return () => {
            this.unsubscribe(messageType, listener);
        };
    }

    // ÂèñÊ∂àËÆ¢ÈòÖ
    unsubscribe(messageType, listener) {
        if (this.listeners.has(messageType)) {
            const typeListeners = this.listeners.get(messageType);
            const index = typeListeners.indexOf(listener);
            if (index > -1) {
                typeListeners.splice(index, 1);
            }
        }
    }

    // Êñ≠ÂºÄËøûÊé•
    disconnect() {
        if (this.socket) {
            this.socket.close();
            this.socket = null;
            this.isConnected = false;
            this.listeners.clear();
            console.log('WebSocketËøûÊé•Â∑≤Êñ≠ÂºÄ');
        }
    }

    // Ëé∑ÂèñËøûÊé•Áä∂ÊÄÅ
    getConnectionStatus() {
        return this.isConnected;
    }
}

// ÂØºÂá∫Âçï‰æãÂÆû‰æã
export default new WebSocketService();